.sticky-header-recommendations {
  /* position: fixed; */
  top: 10%;
  left: 0;
  padding: 10px;
  background-color: #fff;
  /* box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); */
  z-index: 1000;
  /* display: flex; */
  align-items: center;
  justify-content: space-between;
}

.section-name {
  font-weight: bold;
  margin-right: 10px;
}

.inputs-container {
  display: flex;
  align-items: center;
}

.zipcode-input,
.radius-dropdown {
  padding: 8px;
  margin-right: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.find-button {
  padding: 8px 12px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.find-button:hover {
  background-color: #0056b3;
}

.preferences-select {
  width: 40%;
  margin: auto;
}